@use "../../foundation" as *;
/* =========================================================================
  p-favorite
========================================================================= */
.p-favorite {
  background:$bgColor;
  padding: 32px 0 64px;
  @include mq(lg) {
    padding: 48px 0 120px;
  }
  &__list {
    @include mq(md) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(2, auto);
      column-gap: 2em;
    }
  }
  &__item {
    &:first-child {
      @include mq(md) {
        grid-column: 1 / 3;
        grid-row: 1 / 2;
      }
    }
    &:nth-of-type(2) {
      @include mq(md) {
        grid-column: 1 / 2;
        grid-row: 2 / 3;
      }
    }
    &:nth-of-type(3) {
      @include mq(md) {
        grid-column: 2 / 3;
        grid-row: 2 / 3;
      }
    }
    & + & {
      margin-top: 64px;
    }
  }
  &__heading {
    margin-top: 24px;
    display: flex;
    align-items: center;
  }
  &__num {
    display: inline-block;
    font-family: $font-en;
    font-weight: 700;
    font-size: rem(16px);
    color: $lightColor;
    letter-spacing: 0.1em;
    padding: 1.5em 1em;
    border-radius: 9999px;
    &--01 {
      background-color: $mainColor;
      text-shadow: 1px 1px 2px #999;
    }
    &--02 {
      background-color: $subColor;
      text-shadow: 1px 1px 2px #8a8a8a;
    }
    &--03 {
      background-color: $accentColor;
      text-shadow: 1px 1px 2px #8a8a8a;
    }
  }
  &__title {
    margin-left: 8px;
    transition: filter 0.5s;
  }
  &__en-title, &__ja-title {
    display: block;
  }
  &__en-title {
    font-size: rem(20px);
    font-family: $font-en;
    font-weight: 700;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    line-height: 1;
  }
  &__ja-title {
    font-size: rem(12px);
    font-weight: 700;
    letter-spacing: 0.05em;
  }
  &__container {
    display: flex;
    flex-direction: column;
    &--row {
      @include mq(md) {
        margin-top: 1.5em;
        flex-direction: row-reverse;
      }
    }
  }
  &__text {
    font-size: rem(12px);
    line-height: 2;
    letter-spacing: 0.125em;
    @include mq(md) {
      font-size: rem(16px);
    }
    &--01 {
      @include mq(md) {
        width: 55%;
      }
      @include mq(lg) {
        width: 60%;
      }
    }
  }
  &__figure {
    order: -1;
    display: grid;
    width: 90%;
    margin: 24px auto 16px;
    text-align: center;
    @include mq(md) {
      width: 100%;
    }
    &--01 {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      grid-template-rows: repeat(6, 1fr);
      @include mq(md) {
        max-height: 75vh;
        width: 45%;
        margin: 0 0 0 1em;
        margin-left: 1em;
      }
      @include mq(lg) {
        width: 40%;
      }
    }
  }
  &__img {
    object-fit: cover;
    height: 100%;
    width: 100%;
    &--01 {
      grid-area: 1 / 1 / 9 / 4;
      @include mq(md) {
      grid-column: 1 / 7;
      grid-row: 1 / 5;
      }
    }
    &--02 {
      grid-area: 1 / 4 / 4 / 10;
      @include mq(md) {
      grid-column: 7 / 13;
      grid-row: 1 / 3;
      }
    }
    &--03 {
      grid-area: 4 / 4 / 7 / 10;
      @include mq(md) {
      grid-column: 1 / 7;
      grid-row: 5 / 9;
      }
    }
    &--04 {
      grid-area: 1 / 10 / 9 / 13;
      @include mq(md) {
      grid-column: 7 / 13;
      grid-row: 3 / 9;
      }
    }
  }
}
