@use "../../foundation" as *;
/* =========================================================================
  c-loading
========================================================================= */
.c-loading {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: $accentColor;
  display: grid;
  place-items: center;
  z-index: 999;
  &.is-loaded {
    display: none;
    transition: all 0.3s;
  }
  &__inner {
    text-align: center;
  }
  &__icon {
    width: 64px;
    @include animation (
      $name: loadingIconAnimation,
      $duration: 3s,
      $timing-function: ease-in,
      $iteration-count: infinite
    )
  }
  &__text {
    font-size: rem(32px);
    font-weight: 700;
  }
  &__symbol {
    @include animation (
      $name: flashAnimation,
      $duration: 1.5s,
      $iteration-count: infinite
    )
  }
}

@keyframes loadingIconAnimation {
  0% {
    opacity: 0;
    transform: translate( 24px, -24px);
  }
  20% {
    opacity: 1;
    transform: translate( 0, 0);
  }
  80% {
    opacity: 1;
    transform: translate( 0, 0);
  }
  100% {
    opacity: 0;
    transform: translate( -24px, -24px);
  }
}

@keyframes flashAnimation {
  0% {
    opacity: 0;
  }
  30% {
    opacity: 1;
  }
  70% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
