@use "../../foundation" as *;

/* =========================================================================
  p-fv
========================================================================= */
.p-fv {
  background-color: $accentColor;
  &__container {
    height: 100vh;
    position: relative;
  }
  &__title {
    font-size: rem(32px);
    letter-spacing: 0.1em;
    font-weight: 700;
    padding-top: 24px;
    color: $lightColor;
    padding-top: 4vh;
    text-shadow: 2px 2px 3px $darkColor;
  }
  &__subtitle {
    font-size: rem(96px);
    font-weight: 700;
    text-transform: uppercase;
    color: $lightColor;
  }
  &__char {
    position: absolute;
    z-index: 10;
    text-shadow: 2px 2px 3px $darkColor;
    mix-blend-mode: overlay;
    &--01 {
      top: 12%;
      left: 2%;
      @include animation(
        $name: floatingAnimation,
        $duration: 10s,
        $iteration-count: infinite
      );
    }
    &--02 {
      top: 20%;
      left: 30%;
      @include animation(
        $name: floatingAnimation,
        $duration: 10s,
        $delay: 6s,
        $iteration-count: infinite
      );
      @include mq(lg) {
        top: 20%;
        left: 10%;
      }
    }
    &--03 {
      top: 15%;
      right: 19%;
      @include animation(
        $name: floatingAnimation,
        $duration: 10s,
        $delay: 2s,
        $iteration-count: infinite
      );
      @include mq(lg) {
        top: 16%;
        right: 76%;
      }
    }
    &--04 {
      top: 28%;
      right: 0;
      @include animation(
        $name: floatingAnimation,
        $duration: 10s,
        $delay: 5s,
        $iteration-count: infinite
      );
      @include mq(lg) {
        top: 22%;
        right: 65%;
      }
    }
    &--05 {
      bottom: 32%;
      left: 5%;
      @include animation(
        $name: floatingAnimation,
        $duration: 10s,
        $iteration-count: infinite
      );
      @include mq(lg) {
        bottom: 12%;
        left: 68%;
      }
    }
    &--06 {
      bottom: 22%;
      left: 14%;
      @include animation(
        $name: floatingAnimation,
        $duration: 10s,
        $delay: 4s,
        $iteration-count: infinite
      );
      @include mq(lg) {
        bottom: 15%;
        left: 76%;
      }
    }
    &--07 {
      bottom: 12%;
      left: 35%;
      @include animation(
        $name: floatingAnimation,
        $duration: 10s,
        $iteration-count: infinite
      );
      @include mq(lg) {
        bottom: 20%;
        left: 83%;
      }
    }
    &--08 {
      bottom: 18%;
      left: 55%;
      @include animation(
        $name: floatingAnimation,
        $duration: 10s,
        $delay: 1s,
        $iteration-count: infinite
      );
      @include mq(lg) {
        bottom: 8%;
        left: 90%;
      }
    }
    &--09 {
      bottom: 8%;
      right: 8%;
      @include animation(
        $name: floatingAnimation,
        $duration: 10s,
        $iteration-count: infinite
      );
      @include mq(lg) {
        bottom: 12%;
        right: 0;
      }
    }
  }
  &__dice-wrapper {
    position: absolute;
    z-index: 1;
    left: 50%;
    bottom: 25%;
    transform: translateX(-50%);
    &::before {
      content: "";
      width: 80%;
      height: 15%;
      background-color: #7697A5;
      position: absolute;
      bottom: -100px;
      left: 50%;
      transform: translateX(-50%);
      border-radius: 50%;
      z-index: 1;
      filter: blur(2px);
      @include animation($name: blurAnimation);
    }
  }
  &__dice {
    display: block;
    position: relative;
    margin:30px auto 0;
    width: 200px;
    height: 200px;
    transform-style: preserve-3d;
    transform:rotateX(360deg) rotateY(360deg) rotateZ(720deg);
    z-index: 5;
    &.is-loaded {
      animation: loadAnimation 4s linear 1,rotateAnimation 20s 4s linear infinite;
    }
    @include mq(lg) {
      width: 300px;
      height: 300px;
    }
  }
  &__parts {
    position:absolute;
    left:0;
    right:0;
    width:200px;
    height:200px;
    z-index: 5;
    @include mq(lg) {
      width: 300px;
      height: 300px;
    }
    &:nth-child(1){
      transform:translate3d(0, -100px, 0) rotateX(-90deg);
      @include mq(lg) {
        transform:translate3d(0, -150px, 0) rotateX(-90deg);
      }
    }
    &:nth-child(2){
      transform:translate3d(0, 0, 100px);
      @include mq(lg) {
        transform:translate3d(0, 0, 150px);
      }

    }
    &:nth-child(3){
      transform:translate3d(100px, 0, 0) rotateY(90deg);
      @include mq(lg) {
        transform:translate3d(150px, 0, 0) rotateY(90deg);
      }
    }
    &:nth-child(4){
      transform:translate3d(-100px, 0, 0) rotateY(-90deg);
      @include mq(lg) {
        transform:translate3d(-150px, 0, 0) rotateY(-90deg);
      }
    }
    &:nth-child(5){
      transform:translate3d(0, 0, -100px) rotateY(180deg);
      @include mq(lg) {
        transform:translate3d(0, 0, -150px) rotateY(180deg);
      }
    }
    &:nth-child(6){
      transform:translate3d(0, 100px, 0) rotateX(-90deg);
      @include mq(lg) {
        transform: translate3d(0, 150px, 0) rotateX(-90deg);
      }
    }
  }
  &__img {
    width: 200px;
    height: 200px;
    object-fit: cover;
    @include mq(lg) {
      width: 300px;
      height: 300px;
    }
  }
  &__intro {
    text-align: center;
    letter-spacing: 0.1em;
    background-color: $lightColor;
  }
  &__text {
    padding: 10vh 0;
    font-weight: 700;
    line-height: 3;
    @include mq(md) {
      padding: 20vh 0;
      font-size: rem(24px);
    }
  }
}
@keyframes loadAnimation {
  0% {
    transform: translateY(-300%) scale(1.4) rotate3d(1, 1, 1, -60deg);
    transition-timing-function: ease-in;
  }
  25% {
    transform: translateY(0) scale(1.3)  rotate3d(1, 1, 1, -120deg);
    transition-timing-function: ease-out;
  }
  47% {
    transform: translateY(-100%) scale(1.2) rotate3d(1, 1, 1, -180deg);
    transition-timing-function: ease-in;
  }
  67% {
    transform: translateY(0) scale(1.1) rotate3d(1, 1, 1, -240deg);
    transition-timing-function: ease-out;
  }
  85% {
    transform: translateY(-50%) scale(1) rotate3d(1, 1, 1, -300deg);
    transition-timing-function: ease-in;
  }
  100% {
    transform: translateY(0) rotate3d(1, 1, 1, -360deg);
    transition-timing-function: ease-out;
  }
}

@keyframes blurAnimation {
  0% {
    transform: translateX(-50%) scale(1.4);
    transition-timing-function: ease-in;
  }
  25% {
    transform: translateX(-50%) scale(0.8);
    transition-timing-function: ease-out;
  }
  47% {
    transform: translateX(-50%) scale(1.2);
    transition-timing-function: ease-in;
  }
  67% {
    transform: translateX(-50%) scale(0.8);
    transition-timing-function: ease-out;
  }
  85% {
    transform: translateX(-50%) scale(1.1);
    transition-timing-function: ease-in;
  }
  100% {
    transform: translateX(-50%) scale(1);
    transition-timing-function: ease-out;
  }
}

@keyframes rotateAnimation {
  0% {
    transform:rotate3d(0);
  }
  100% {
    transform:rotate3d(1, 1, 1, 360deg);
  }
}

@keyframes floatingAnimation {
  0% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(10px);
  }
  60% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0);
  }
}
