@use "../../foundation" as *;
/* =========================================================================
  p-profile
========================================================================= */
.p-profile {
  padding-top: 10vh;
  &__title {
    position: relative;
    z-index: 10;
    font-size: rem(16px);
    font-weight: 700;
    @include mq(md) {
      font-size: rem(24px);
    }
  }
  &__list {
    position: relative;
    z-index: 10;
    margin-top: 1.5em;
  }
  &__item {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    & + & {
      margin-top: 1em;
    }
  }
  &__term {
    width: 96px;
    font-family: $font-en;
    @include mq(md) {
      width: 120px;
    }
  }
  &__subitem {
    &::before {
      content: "ãƒ»";
      margin-right: 0.25em;
    }
    & + & {
      margin-top: 0.5em;
    }
  }
  &__figure {
    position: absolute;
    top: 16vh;
    max-width: 320px;
    width: 60%;
    right: 0;
    @include mq(sm) {
      right: none;
      left: 240px;
    }
    @include mq(md) {
      left: 320px;
    }
      img {
        border-radius: 67% 33% 36% 64% / 70% 70% 30% 30%;
        position: relative;
      &::after {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-color: rgba(245, 245, 245, 0.2);
        border-radius: 67% 33% 36% 64% / 70% 70% 30% 30%;
      }
    }
  }
}
