@use '../foundation' as *;

/* =========================================================================
  l-header
========================================================================= */
.l-header {
  &__hamburger-btn {
    position: fixed;
    top: 1em;
    right: 24px;
    display: inline-block;
    text-decoration: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-color: transparent;
    width: 32px;
    height: 32px;
    text-align: right;
    z-index: 100;
    mix-blend-mode: exclusion;
  }
  &__hamburger-line {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    width: 24px;
    height: 3px;
    border-radius: 3px;
    background-color: rgb(189, 143, 122);
    transition: all 0.3s;
    animation: closeAnimation 0.6s 1;
    animation-fill-mode: backwards;
    &::before {
      content: "";
      position: absolute;
      display: block;
      background-color: inherit;
      height: 100%;
      border-radius: 3px;
      width: 32px;
      top: -10px;
      right: 0;
      transition: all 0.3s ease-in-out;
    }
    &.is-active {
      width: 24px;
      transform: translateY(-5px)rotate(45deg);
      transition: all 0.1s;
      animation: openAnimation 0.6s 1;
      animation-fill-mode: backwards;
      &::before {
        width: 24px;
        transform: translate(0,10px)rotate(-90deg);
        transition: all 0.1s;
      }
    }
  }
  &__modal {
    position: fixed;
    z-index: 90;
    top: 0;
    right: 0;
    bottom: 0;
    transform: translateX(calc(100% + 52px));
    transition: ease-in 0.3s;
    background-color: rgba(172,198,207,0.3);
    backdrop-filter: blur(20px);
    border-left: 1px solid rgba(255, 255, 255, 0.4) ;
    @include mq(md) {
      transform: translateX(calc(100% + 68px));
    }
    &.is-active {
      transform: translateX(0);
      transition: ease-in 0.3s;
    }
