  }
  &__nav {
    width: 60vw;
    @include mq(sm) {
      width: 50vw;
    }
    @include mq(md) {
      width: 40vw;
    }
  }
  &__menu {
    height: 100%;
    padding: 10vh 0 0 1em;
  }
  &__item {
    font-size: rem(16px);
    @include mq(md) {
      font-size: rem(24px)
    }
    & + & {
      margin-top: 3vh;
      @include mq(lg) {
        margin-top: 2vh;
      }
    }
  }
  &__link {
    transition: color 0.3s;
    &:hover {
      color: $accentColor;
      transition: color 0.3s;
    }
  }
}


@keyframes openAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes closeAnimation {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
