@use "../../foundation" as *;
/* =========================================================================
  p-achievement
========================================================================= */
.p-achievement {
  background: $bgColor;
  padding: 32px 0 64px;
  @include mq(lg) {
    padding: 48px 0 120px;
  }
  &__text {
    margin-top: 24px;
    text-align: center;
  }
  &__container {
    margin-top: 24px;
  }
  &__slider {
    padding-bottom: 40px;
  }
  &__slider-wrapper {
    transition: filter 0.5s;
    transition-duration: 0.5s;
  }
  &__slider-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include mq(md) {
      flex-direction: row;
      padding-bottom: 40px;
    }
  }
  &__data {
    margin-top: 1em;
    @include mq(md) {
      width: 50%;
      padding: 1em;
    }
  }
  &__heading {
    display: none;
    text-transform: uppercase;
    @include mq(md) {
      display: block;
    }
  }
  &__list {
    @include mq(md) {
      margin-top: 1em;
    }
  }
  &__item {
    text-align: center;
    @include mq(md) {
      text-align: left;
    }
    & + & {
      margin-top: 16px;
    }
  }
  &__term {
    font-size: rem(12px);
    font-weight: 700;
    @include mq(md) {
      font-size: rem(16px);
    }
  }
  &__discription {
    span {
      display: inline-block;
    }
    &--alignleft {
      text-align: left;
    }
  }
  &__figure {
    order: -1;
    text-align: center;
    overflow: hidden;
    max-width: 400px;
    width: 80%;
    @include mq(md) {
      width: 50%;
      align-self: center;
      margin-right: 3vw;
    }
  }
  &__img-link {
    display: block;
  }
  &__img {
    text-align: center;
    object-fit: cover;
    aspect-ratio: 3 / 2;
    transition: all 0.3s;
    vertical-align: bottom;
    &:hover {
      opacity: 0.8;
      transform: scale(1.1);
      transition: all 0.3s;
    }
  }
  &__link {
    display: flex;
    justify-content: flex-end;
    margin-top: 16px;
    @include mq(md) {
      margin-top: 32px;
    }
  }
  &__btn {
    font-size: rem(16px);
    @include mq(md) {
      font-size: rem(24px);
    }
    & + & {
      margin-left: 16px;
    }
  }
  &__icon {
    $this: &;
    display: flex;
    align-items: center;
    transition: color 0.3s;
    &:hover svg {
      color: $accentColor;
      transition: color 0.3s;
    }
  }
  svg {
    margin-left: 0.25em;
    width: 24px;
    height: 24px;
    color: #666;
    transition: color 0.3s;
  }
}
