@use "../../foundation" as *;
/* =========================================================================
  .c-heading
========================================================================= */
.c-heading {
  font-family: $font-en;
  font-size: rem(24px);
  letter-spacing: 0.1em;
  font-weight: 700;
  @include mq(md) {
    font-size: rem(48px);
  }
  &--blue {
    color: $accentColor;
  }
  &--white {
    position: relative;
    z-index: 10;
    color: $lightColor;
  }
  &__jp {
    font-size: 0.5em;
    font-family: $font-jp;
    @include mq(md) {
      font-size: rem(16px);
    }
  }
}
