@use "../../foundation" as *;
/* =========================================================================
  c-scrolltop
========================================================================= */
.c-scrolltop {
  $this: &;
  position: fixed;
  z-index: 10;
  bottom: 8%;
  right: 4%;
  width: 40px;
  height: 40px;
  text-align: center;
  opacity: 0;
  transition: all 0.5s;
  transform: translate(8px, -8px);
  &.is-show {
    opacity: 1;
    transition: all 0.5s;
    transform: translate(0, 0);
  }
  &__icon {
    filter: drop-shadow(2px 2px 4px $lightColor);
    transition: transform 0.3s;
  }
  &__text {
    color: $subColor;
    font-weight: 700;
    text-shadow: 1px 1px #ccc;
  }
}
