@use "../../foundation" as *;
/* =========================================================================
  p-lastly
========================================================================= */
.p-lastly {
  position: relative;
  padding: 32px 0 64px;
  width: 100%;
  @include mq(lg) {
    padding: 48px 0 120px;
  }
  &::after {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.4);
  }
  &::before {
    content:"";
    display:block;
    position:fixed;
    top:0;
    left:0;
    right: 0;
    bottom: 0;
    z-index:-1;
    height: 100vh;
    background-image: url("#{$imgPath}webp/lastlyBg02.webp");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center bottom;
    @include mq(md) {
      background-image: url("#{$imgPath}webp/lastlyBg.webp");
    }
  }
  &__inner {
    margin-top: 32px;
    color: $lightColor;
    position: relative;
    z-index: 10;
    @include mq(md) {
      width: 80%;
      margin-right: auto;
      margin-left: auto;
    }
  }
  &__content {
    line-height: 1.75;
    @include mq(md) {
      line-height: 2;
      text-align: center;
    }
    & + & {
      margin-top: 1.5em;
    }
  }
  &__name {
    margin-top: 2em;
    @include mq(md) {
      text-align: center;
    }
  }
}
