@use "../foundation" as *;
/* =========================================================================
  swiper
========================================================================= */
.swiper-pagination-bullet {
  background-color: $lightColor;
  opacity: 1;
}
.swiper-pagination-bullet-active {
  background-color: $accentColor;
}

.swiper-button-prev {
  width: 48px;
  height: 24px;
  top: 50%;
  left: 0;
  @include mq(xs) {
    top: 55%;
  }
  @include mq(sm) {
    top: 60%;
  }
  @include mq(md) {
    width: 64px;
    top: 98%;
    left: 33%;
  }
  &:after {
    content: "";
    width: inherit;
    height: inherit;
    background-image: url(#{$imgPath}webp/btn-prev.webp);
    background-size: contain;
    background-repeat: no-repeat;
  }
}
.swiper-button-next {
  width: 48px;
  height: 24px;
  top: 50%;
  right: 0;
  @include mq(xs) {
    top: 55%;
  }
  @include mq(sm) {
    top: 60%;
  }
  @include mq(md) {
    width: 64px;
    top: 98%;
    right: 33%;
  }
  &:after {
    content: "";
    width: inherit;
    height: inherit;
    background-image: url(#{$imgPath}webp/btn-next.webp);
    background-size: contain;
    background-repeat: no-repeat;
  }
}
