@use "../../foundation" as *;
/* =========================================================================
  p-future
========================================================================= */
.p-future {
  background-color: $lightColor;
  padding: 32px 0 64px;
  @include mq(lg) {
    padding: 48px 0 120px;
  }
  &__heading {
    margin-top: 24px;
    text-align: center;
    font-size: rem(16px);
    letter-spacing: 5%;
    transition: filter 0.5s;
    position: relative;
    z-index: 10;
    @include mq(md) {
      font-size: rem(24px);
    }
  }
  &__inner {
    position: relative;
  }
  &__content {
    position: relative;
    z-index: 10;
    margin-top: 8px;
    padding: 0.75em 1.5em;
    backdrop-filter: blur(1px);
    border: 1px solid rgba(255, 255, 255, 0.2);
    background-color: rgba($color: $accentColor, $alpha: 0.2);
    @include mq(sm) {
      width: 80%;
      max-width: 960px;
      margin-right: auto;
      margin-left: auto;
      margin-top: 24px;
      padding: 0.25em 1em;
      line-height: 2;
    }
  }
  &__figure {
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    opacity: 0.8;
  }
  &__img {
    position: absolute;
    z-index: 1;
    &--01 {
      width: 42%;
      bottom: -10%;
      right: -2%;
      @include mq(sm) {
        width: 40%;
        right: -2%;
        bottom: -8%;
      }
      @include mq(md) {
        right: 0;
        bottom: -7%;
        width: 35%;
      }
      @include mq(lg) {
        width: 28%;
      }
    }
    &--02 {
      width: 26%;
      top: 5%;
      left: -2%;
      @include mq(sm) {
        width: 24%;
        top: 10%;
        left: 5%;
      }
      @include mq(md) {
        width: 22%;
      }
      @include mq(lg) {
        width: 18%;
        top: 12%;
      }
    }
  }
}
