@use "../../foundation" as *;
/* =========================================================================
  p-motive
========================================================================= */
.p-motive {
  background-color: $lightColor;
  padding: 32px 0 64px;
  @include mq(lg) {
    padding: 48px 0 120px;
  }
    &__item {
      $this: &;
      position: relative;
      & + & {
        margin-top: 64px;
        @include mq(lg) {
          margin-top: 96px;
        }
      }

    }
  &__heading {
    margin-top: 5vh;
    display: flex;
    justify-content: center;
    align-items: center;
    @include mq(md) {
      margin-top: 48px;
    }
    &--01,&--02 {
        &::before,&::after {
          content: "";
          display: inline-block;
          background-repeat: no-repeat;
          background-size: contain;
        }
      }
      &--01 {
        &::before {
          width: 40px;
          height: 32px;
          background-image: url(#{$imgPath}webp/kamaboko.webp);
          transform: rotate(-15deg);
          margin-right: 1em;
          @include mq(md) {
            margin-right: 2em;
            width: 64px;
          }
        }
        &::after {
          width: 40px;
          height: 32px;
          background-image: url(#{$imgPath}webp/motorcycle.webp);
          transform: rotate(15deg);
          margin-left: 1em;
          @include mq(md) {
            margin-left: 2em;
            width: 64px;
          }
        }
      }
      &--02 {
        &::before {
          width: 32px;
          height: 36px;
          background-image: url(#{$imgPath}webp/gamingchair.webp);
          transform: rotate(-15deg);
          margin-right: 1em;
          @include mq(md) {
            margin-right: 2em;
            width: 48px;
            height: 48px;
          }
        }
        &::after {
          width: 32px;
          height: 36px;
          background-image: url(#{$imgPath}webp/lantern.webp);
          transform: rotate(15deg);
          margin-left: 1em;
          @include mq(md) {
            margin-left: 2em;
            width: 48px;
            height: 56px;
          }
        }
      }
    span {
      font-size: rem(20px);
      font-family: $font-en;
      font-weight: 700;
      margin-right: 0.25em;
    }
  }
  &__content {
    margin-top: 24px;
    line-height: 2;
    @include mq(md) {
      width: 60%;
      margin-left: auto;
      margin-right: auto;
    }
  }
}
