@use "../../foundation" as *;
/* =========================================================================
  c-introduce
========================================================================= */
.c-introduce {
  margin-top: 96px;
  &__title {
    font-size: rem(16px);
    font-weight: 700;
    @include mq(md) {
      font-size: rem(24px);
    }
  }
  &__wrapper {
    display: flex;
    align-items: center;
  }
  &__icon {
    text-align: center;
  }
  &__icon-img {
    width: 48px;
    @include animation(
      $name: iconMove,
      $duration: 5s,
      $timing-function: ease-in,
      $iteration-count: infinite
    );
    @include mq(md) {
      width: 64px;
    }
  }
  &__icon-text {
    margin-top: 0.5em;
    font-size: rem(12px);
    @include mq(md) {
      font-size: rem(14px);
    }
  }
  &__list {
    margin: 2em 0 0 2em;
    overflow-x: auto;
    white-space: nowrap;
    padding: 0 1em 1em ;
    -webkit-overflow-scrolling: touch;
    scrollbar-width: thin;
    scrollbar-color: rgba($color: $accentColor, $alpha: 0.6) rgba($color: $lightColor, $alpha: 0.4);
    &::-webkit-scrollbar {
      background-color: rgba($color: $lightColor, $alpha: 0.4);
      border-radius: 12px;
      height: 8px;
    }
    &::-webkit-scrollbar-thumb {
      background-color: rgba($color: $accentColor, $alpha: 0.6);
      border-radius: 12px;
    }
  }
  &__item {
    display: inline-block;
    width: 80%;
    vertical-align: top;
    white-space: normal;
    @include mq(sm) {
      width: 45%;
    }
    @include mq(md) {
      width: 40%;
    }
    @include mq(lg) {
      width: 30%;
    }
    & + & {
      margin-left: 0.5em;
    }
  }
  &__term {
    font-family: $font-en;
  }
  &__description {
    background-color: rgba($color: #fefefe, $alpha: 0.4);
    padding: 0.5em;
    border-radius: 3px;
    box-shadow: 1px 2px 6px #ccc;
    height: calc(100% - 1.25em);
  }
  &__subitem {
    &::before {
      content: "ãƒ»";
      margin-right: 0.25em;
    }
    & + & {
      margin-top: 0.5em;
    }
  }
}

@keyframes iconMove {
  0% {
    transform: translateX(0);
  }
  20% {
    transform: translateX(0);
  }
  45% {
    transform: translateX(12px);
  }
  50% {
    transform: translateX(9px);
  }
  55% {
    transform: translateX(12px);
  }
  80% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(0);
  }
}
